.popup {
  display: flex;
  /*по брифу*/
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  opacity: 0;
  transition: all 0.2s ease-in-out;
  z-index: 10;
  top: 0;
  left: 0;
  justify-content: center;
  visibility: hidden;
  width: 100%;
  height: 100%;
  align-items: center;
}
